@import "~frontend-scss-mixins/mixins";
@import "../base/mixins";
@import "../base/variables";
@import "../base/media-queries";

.portfolio {
  background: var(--black-color);
  color: var(--white-color);
  padding-top: 14vw;
  padding-bottom: 10vw;

  &__title-block {
    margin-bottom: 6vw;
  }

  &__title {
    @include margin-auto;
    width: fit-content;
    position: relative;
  }

  &__title-text {
    font-size: 15.5810vw;
    line-height: 106%;
    letter-spacing: -0.05em;
    margin-bottom: 2.2vw;
  }

  &__title-text-line {
    padding: 0 2vw;
  }

  &__title-icon {
    width: 2.4vw;
    height: 2.4vw;
    display: block;
    position: absolute;
    right: 0;
    top: 5.8vw;
  }

  &__subtitle {
    @include manrope;
    @include margin-auto;
    width: fit-content;
    font-weight: 300;
    font-size: 1.7196vw;
    line-height: 100%;
    letter-spacing: -0.026em;
  }

  &__slider {
    height: 700px;
    margin-bottom: 4.4vw;
    padding: 0 30px;
    width: 100%;
    position: relative;
    overflow: hidden;

    .swiper {
      width: 100%;
      height: 550px;
    }

    .swiper-pagination {
      bottom: -10px;
      --swiper-pagination-bullet-inactive-color: #fff;
      --swiper-pagination-color: #fff;
      display: none;
    }

    // prettier-ignore
    .swiper-slide {
      -webkit-box-reflect: below 10px -webkit-linear-gradient(
                      bottom, rgba(255, 0, 0, 0.1) 0%,
                      transparent 32px,
                      transparent 100%
      );
    }
  }

  &__slider-img {
    //width: 100%;
    //height: 24.4919vw;\
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    object-position: center;
    pointer-events: none;
  }

  &__desc {
    @include flex-row;
    padding-left: 55vw;
    margin-bottom: 2vw;
    position: relative;
    padding-top: 40vw;
  }

  &__desc-item {
    @include manrope;
    letter-spacing: -0.033em;

    &--1 {
      font-size: 8.4392vw;
      width: 100%;
      position: absolute;
      top: 0;
      left: 50%;
      padding-top: 0;
      transform-origin: left;
      z-index: 4;
      transform: translateX(-50%)
    }

    &--2 {
      font-size: 1.7196vw;
      margin-left: 6vw;
      width: 18.5vw;
    }

  }

  $desc-item-1: '.portfolio__desc-item--1';


  &__desc-item-inner {
    margin-top: -1.33vw;
    display: flex;

    #{$desc-item-1} & {
      margin-top: -3.9vw;
    }

    &--2 {
      margin-top: -0.33vw;
    }

    &--main-color {
      color: var(--main-color);
    }
  }

  &__desc-item-inner-line {

  }

  &__chart {
    width: 100%;
    position: relative;
    height: 25vw;
    margin-bottom: 4vw;
    overflow: hidden;
  }

  &__chart-mask {
    position: absolute;
    width: 110%;
    right: 0;
    top: 0;
    height: 100%;
    background: linear-gradient(76deg, rgba(2, 3, 5, 0) 0%, rgba(2, 3, 5, 1) 10%, rgba(2, 3, 5, 1) 100%);
    background: var(--black-color);
    z-index: 3;
  }

  &__chart-line {
    position: absolute;
    height: 0.13vw;
    background: var(--main-color);
    transform-origin: left center;

    &--1 {
      width: 7.3vw;
      left: -1vw;
      top: 23.55vw;
      transform: rotate(-16deg);
    }

    &--2 {
      width: 18.3vw;
      left: 5.9vw;
      top: 21.6vw;
      transform: rotate(-19.6deg);
    }

    &--3 {
      width: 17vw;
      left: 22.9vw;
      top: 15.6vw;
      transform: rotate(3deg);
    }

    &--4 {
      width: 19.6vw;
      left: 40.1vw;
      top: 16.6vw;
      transform: rotate(-28.8deg);
    }

    &--5 {
      width: 17vw;
      left: 57.3vw;
      top: 7.2vw;
      transform: rotate(5.7deg);
    }

    &--6 {
      width: 21.7vw;
      left: 74.3vw;
      top: 9vw;
      transform: rotate(-21.9deg);
    }

    &--7 {
      width: 20vw;
      left: 94.3vw;
      top: 1vw;
      transform: rotate(-10.9deg);
    }
  }

  &__chart-dot {
    width: 0.63vw;
    height: 0.63vw;
    border-radius: 50%;
    position: absolute;
    background: var(--main-color);
    transform: translate(-50%, -50%);
    z-index: 1;

    &--1 {
      left: 5.9vw;
      top: 21.6vw;
    }

    &--2 {
      left: 22.9vw;
      top: 15.6vw;
    }

    &--3 {
      left: 40.1vw;
      top: 16.6vw;
    }

    &--4 {
      left: 57.3vw;
      top: 7.2vw;
    }

    &--5 {
      left: 74.3vw;
      top: 9vw;
    }

    &--6 {
      left: 94.3vw;
      top: 1vw;
    }

  }

  &__link-block {
    @include manrope;
    font-weight: 700;
    font-size: 6.9306vw;
    line-height: 100%;
    letter-spacing: -0.033em;
    padding-left: 26.5vw;
  }

  &__link-title {
    margin-bottom: 3vw;
  }

  &__link-item {
    @include manrope();
    line-height: 100%;
    color: var(--main-color);
    letter-spacing: -0.033em;
    font-weight: 700;
    position: relative;
    width: fit-content;

    &:after {
      @include pseudo-absolute();
      width: 100%;
      height: 0.65vw;
      background: var(--main-color);
      left: 0;
      top: calc(100% + 1vw);
    }
  }
}
